@font-face {
  font-family: Avenir;
  src: url("Avenir-Heavy.01d9ab74.ttf") format("truetype");
  font-weight: 400;
}

@font-face {
  font-family: AvenirBook;
  src: url("Avenir-Book.657c3568.ttf") format("truetype");
  font-weight: 300;
}

html {
  --place-holder-color: #3d4e61;
  --border-color: #e1e7ed;
  --white-color: #fff;
  font-family: Avenir, Helvetica, sans-serif;
  font-size: 16px;
  font-weight: 300;
}

.search-bar {
  margin-top: 20px;
  position: relative;
}

.search-bar:hover {
  background: var(--white-color);
  border-radius: 4px;
  box-shadow: 0 3px 8px #3d4e6133;
}

.search-bar__input {
  border: 1px solid var(--border-color);
  color: var(--place-holder-color);
  border-radius: 4px;
  width: 100%;
  font-family: Avenir, Helvetica, sans-serif;
  font-size: 14px;
  position: relative;
  box-shadow: 0 1px 8px #3d4e611a;
}

.search-bar__input:focus {
  background: linear-gradient(#fff 0%, #f6f6f7 100%);
  border-radius: 3px;
  outline: none;
}

.search-bar__input::placeholder {
  color: var(--place-holder-color);
  font-family: AvenirBook, Helvetica, sans-serif;
  font-weight: 300;
}

.search-bar--big input {
  color: var(--place-holder-color);
  height: 70px;
  padding-left: 62px;
  font-family: inherit;
  font-size: 16px;
  font-weight: 400;
}

.search-bar:before {
  z-index: 1;
  content: "";
  background-image: url("Search.ccd3c07b.svg");
  background-size: contain;
  position: absolute;
}

.search-bar--small input {
  height: 42px;
  padding-left: 33px;
  font-size: 14px;
}

.search-bar--big:before {
  width: 19px;
  height: 19px;
  top: 25px;
  left: 26px;
}

.search-bar--small:before {
  width: 11px;
  height: 11px;
  top: 15px;
  left: 13px;
}

